
import React, { Component, PureComponent } from 'react'
import {withRouter, Route, HashRouter as Router, Switch, } from "react-router-dom"
import { connect } from "react-redux"
import { 
  login,  
} from '../../actions/login'// 
import AntdTable from '../../components/AntdTable'// 
import Collapses from '../../components/Collapses'// 
import {ANIMATE,} from 'constants'// 

import SubPage1 from '../SubPage1'
import SubPage2 from '../SubPage2'

import { 
  Button, 
  Row, 
  Col,
} from 'antd';
import './style.less'



@connect(
  v => v, 
  {
    login,
  }
)
class Auth extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      loading: false,
    }
  }
  login = (e,  ) => {
    console.log('    login ： ', e, this.state, this.props,   )
    this.props.login({
      isAuth: true,
    })
  }
  go = (e,  ) => {
    console.log('    go ： ', e, this.state, this.props,   )
    const {
      url,
    } = this.props.match
    const path = `${url}/${e}`
    console.log(' path ： ', path,  )// 
    this.props.history.push(path)
  }
  componentDidMount() {
    console.log('  组件componentDidMount挂载 ： ', this.state, this.props,  )// 
    // setTimeout(() => {
    //   console.log('  延时器 ： ',  )
    //   this.setState({
    //     loading: !this.state.loading,
    //   })
    // }, 3000)
    
  }
  
  
  render() {
    console.log(' %c Auth 组件 this.state, this.props ： ', `color: #333; font-weight: bold`, this.state, this.props,  )
    const {loading,  } = this.state// 
    return <Row>
      
      <Col span={12}  >
        <Collapses 
            animate={ANIMATE.bounceInLeft}
            noLimit 
            title={'工藝路線'} 
            extra={
              <div className="btnWrapper">
                <Button type="primary" onClick={this.login}>登录</Button>
              </div>
            }
            loading={loading}
          >
          <div className='Auth'>
            <Button type="primary" onClick={this.login}>Primary</Button>


            {/* <Route name={'222'} path={'/Auth/SubPage1'} component={SubPage1}></Route>
            <Route name={'name'} path={'/Auth/SubPage2'} component={SubPage2}></Route> */}


            <Button type="primary" onClick={() => this.go('SubPage1')}>SubPage1</Button>
            <Button type="primary" onClick={() => this.go('SubPage2')}>SubPage2</Button>

          </div>
        </Collapses>
      </Col>

      <Col span={12}  >
          
        <Collapses 
            animate={ANIMATE.bounceInRight}
            noLimit 
            title={'22'} 
            extra={
              <div className="btnWrapper">
                <Button type="primary" onClick={this.login}>登录</Button>
              </div>
            }
            loading={loading}
          >
            <AntdTable></AntdTable>
        </Collapses>
      </Col >


    </Row> 
  }
}

export default Auth// 


