
import React, { Component, PureComponent } from 'react'
import './style.less'
import { 
  Skeleton,
  Card,
  Collapse,
  Icon,
  Avatar,
} from 'antd'

import { 
  ANIMATE, 
} from 'constants'
const { Panel, } = Collapse
const { 
  Meta, 
} = Card



class InteliPanel extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      activeKey: '1',
      isClose: false,
    }
  }
  callback = _ => {
    let { activeKey } = this.state
    activeKey = activeKey === '1' ? '2' : '1'
    this.setState({ activeKey, })
  }
  close = _ => {
    _.stopPropagation()
    this.setState({ isClose: true, activeKey: '2', })
  }
  render() {
    const { activeKey, isClose } = this.state
    const { title, children, className, extra, animate, noAnimate, auth, noLimit, loading,  } = this.props
    console.log('InteliPanel组件 ：', this.props, activeKey, noAnimate, noAnimate ? 11 : 22, auth, auth && !noLimit, auth || noLimit ? extra : null)
    const animation = !noAnimate ? animate != undefined ? animate : ANIMATE.flipinX : '' 
    // console.log(' animation ： ', animation, noAnimate, animate )// 
    return isClose ? null : <Collapse
      className={`collapses ${animation} ${className} `}
      defaultActiveKey={['1']}
      onChange={this.callback}
      expandIconPosition={'right'}
    >
      <Panel header={<div className='d-ib title' >{title}</div>} key="1" extra={
        [
          extra,
          <Icon
            className='m-l10' 
            key={'close'}
            type="close-circle"
            onClick={this.close}
          />,
        ]
      }>
        <Skeleton loading={loading} avatar active>
          {children}
        </Skeleton>
      </Panel>
    </Collapse> 
  }
}

export default InteliPanel

