
import React, { Component, PureComponent } from 'react'
import './style.less'
import {
  withRouter, 
  Route, 
  HashRouter as Router, 
  Switch, 

} from "react-router-dom"
import { 
  Icon, 
  Layout, 
  Button, 
  Tooltip,

} from 'antd'
const { Header } = Layout


class Headers extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      collapsed: false,
    }
  }
  logout = (v) => {
    console.log('v  logout ：', v  )
  }

  render() {
    const {
      collapsed,
      toggle, 
    } = this.props
    console.log('Header 组件 this.state, this.props ：', this.state, this.props,  )
    return (
      <Header className={`headerWrapepr ${collapsed ? 'isCollapse' : ''}`} >
        <Icon
          className="trigger"
          type={collapsed ? 'menu-unfold' : 'menu-fold'}
          onClick={toggle}
        />
          <div className="logoutWrapper">
            <Tooltip title="退出登錄">
              <Icon type="poweroff" onClick={this.logout} />
            </Tooltip>
          </div>
      </Header>
    )
  }
}

export default Headers