import React, { Component, PureComponent } from 'react'
import './style.less'
import { Table, Icon, notification, Modal, Button, Tag  } from 'antd'

class Dialogs extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
    }
  }
  handleOk = (e) => {
    this.props.handleOk()
  }
  handleClose = (e) => {
    this.props.close()
  }
  render() {
    const { 
      show, 
      children, 
      title, 
      width, 
      className, 
      noJustify, 
      isHideOk, 
      okTxt, 
      extra, 
      cancelTxt, 
      maskClosable,  
    } = this.props

    console.log(' %c Dialogs 组件 this.state, this.props ： ', `color: #333; font-weight: bold`, this.state, this.props,  )// 

    return (
      <Modal className={`${className} ${noJustify ? '' : 'textJustify'}`}
        title={title}
        width={width != undefined ? width : '60%'}
        visible={show}
        onOk={this.handleOk}
        onCancel={this.handleClose} 
        maskClosable={maskClosable}
        footer={[
          <Button key="cancel" onClick={this.handleClose} icon="meh-o">{cancelTxt != undefined ? cancelTxt : '取消'}</Button>,
          extra != undefined ? extra : null,
          !isHideOk ? <Button key="sure" onClick={this.handleOk} type="primary" icon="smile-o">{okTxt != undefined ? okTxt : '確認'}</Button> : null,
        ]}
      >
        {children}
      </Modal>
    )
  }
}

export default Dialogs