import React, { Component, PureComponent } from 'react'
import './style.less'
import { Table, Icon, Switch, Radio, Form, Divider } from 'antd'
import { 
  SIZE,
  ANIMATE, 
} from 'constants'// 
import { 
  showTotal, 
} from 'utils'// 



class InteliTable extends PureComponent {
  constructor(props) {
    super(props)
    const { 
      total,
    } = this.props 

    const pagination = {
      pageSize: SIZE,
      total,
      showSizeChanger: true,
      showTotal: showTotal,
    }
    this.state = {
      pagination,
    }
  }
  render() {
    console.log(' %c InteliTable 组件 this.state, this.props ： ', `color: #333; font-weight: bold`, this.state, this.props,  )
    const { 
      pagination,
    } = this.state
    const { 
      data,
      columns,
      loading,
      rowKey,
    } = this.props

    return <div className='table'>
      <Table 
        showQuickJumper
        size={'small'}
        dataSource={data}
        pagination={pagination}
        // columns={columns}
        // loading={loading}
        rowClassName={(record, i) => ANIMATE.bounceIn}
        scroll={{ x: 800,  }}
        rowKey={rowKey}
        {...this.props}  
      />
    </div>
  }
}

export default InteliTable// 

