export const get = (url, params) => axios.get(url, {params,  })
export const post = (url, params, o) => axios.post(url, params, o)
// export const blobPost = (url, params, o) => axios.post(url, {...params, noTips: true}, o) 
export const blobPost = (url, params, o) => axios({method: 'post', url, data: {...params, noTips: true, }, responseType: 'blob', })
export const noTipsGet = (url, params) => axios.get(url, {params: {...params, noTips: true}})
export const noTipsPost = (url, params) => axios.post(url, {...params, noTips: true})
