@connect(
   _ => _, 
  {
    logout,
    collapse,
  }
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      collapsed: false,
      current: '1',
      offsetHeight: 0, 
      offsetWidth: 0, 
      scrollTop: 0,
    }
  }
  logout = () => {
    this.props.logout()
    this.props.history.push(`/login`)
  }
  jump = (p, ) => {
    console.log('    jump ： ', p,  )
    this.props.history.push(p,)
  }
  route =  () => {
    const {
      location: {pathname, },  
    } = this.props// 
    const routesConfig = [...router, ...routeArr, ]
    console.log('匹配 routesConfig ： ', routesConfig,  )// 
    const item = routesConfig.find(v => {
      const path = v.path && v.path.toLowerCase()
      const pathnames = pathname.replace('//', '/').toLowerCase()
      console.log(' find v ： ', v, pathname, path, pathnames, path === pathnames )
      return path === pathnames
    })
    console.log(' item 结果 ： ', item,  )// 
    if (item) {
      const {type = 1, name,  } = item
      const crumbs = [name, ]
      
      console.log(' type, page, ： ', type,  )// 
      for (let i = 0, len = type - 1; i < len; i++) {
        const {page,  } = item
        console.log(' 循环   ： ', i, page,  )
        const parent = routesConfig.find(v => {
          console.log('匹配匹配 find v ： ', v, page, v.key, page === v.key,   )
          return page === v.key
        })
        console.log(' parent ： ', parent,  )// 
        const {name, type,  } = parent
        crumbs.push(name)
        console.log('匹配  route  item ： ', crumbs, routesConfig, item, parent,      )
      }
      
      return {
        ...item,
        crumbs: crumbs.reverse()// ,  
      // } || {
      //   crumbs,
      }
    }
    console.log(' 没有 ： ',    )// 
    return null
  }

  render() {
    console.log('App组件 组件 this.state, this.props ：', this.state, this.props, )
    const {word, collapsed, } = this.state
    const {userInfo,  } = this.props

    const {
      location: {pathname, },  
    } = this.props//

    const matchItem = this.route()
    console.log(' matchItem ： ', matchItem,  )// 
    if (!matchItem) return <Route key={'NotFound'} component={NotFound}></Route>// 


    const {
      name = '',
      animate,
      icon,
      noCollapses,
      page,
      fa,
      crumbs,

    } = this.route()


    if (pathname === '/') {
      return <Home></Home> 
    }
    


    
    const container = <Switch>
      {
        homeRoutes.map((v) => {
          // console.log(' v ： ', v,  )// 
          return <Route {...v} name={'namesss'}
          // strict={false}
          // strict
          // sensitive
          /> 
        })
      }



    </Switch>

    console.log(' 匹配 matchItem ： ', container, crumbs, matchItem, pathname, homeRoutes,  )// 

    return (
      <div className="container ">
        {container}



      </div>
    )
  }
}

                  
                  
