import React, { Component, PureComponent } from 'react'
import './style.less'
import { HashRouter as Router, Route, Link, Switch, withRouter, Redirect,  } from 'react-router-dom'//
import { personConfig,  } from 'config'// 

import { 
  Tabs, 
  Input,
  AutoComplete,
  Button,

 } from 'antd';

 const { Option } = AutoComplete;
const { TabPane } = Tabs;
const { Search } = Input





@withRouter
class SearchInput extends Component {
  constructor(props) {
    super(props)
    this.state = {
      dataSource: personConfig,
    }
  }
  onChange = (e,  ) => {
    console.log('    onChange ： ', e,   )
    
  }
  onSearch = (item,  ) => {
    console.log('    onSearch ： ', item,   )
    const dataSource = personConfig.filter((v) => v.key.includes(item) || v.name.includes(item))
    console.log('  dataSource ：', dataSource,  )// 
    this.setState({
      dataSource,
    })
    
  }
  onSelect = (id,  ) => {
    console.log('    onSelect ： ', id, this.state, this.props,   )
    const path = `resume?id=${id}`
    console.log(' path ： ', path,  )// 
    this.props.history.push(path)
  }
  search = (e,  ) => {
    console.log('    search ： ', e,   )
  }

  renderOption = (item,  ) => {
    return (
      <Option key={item.id} text={item.key}>
        <a
          href={`/#/resume?id=${item.id}`}
          target="_blank"
          className='link' 
        >
          {item.name} - 
          <span className="match">
            {item.key}
          </span>
        </a>
      </Option>
    )
  }

  render() {
    console.log(' %c SearchInput 组件 this.state, this.props ： ', `color: #333; font-weight: bold`, this.state, this.props,  )
    const {
      dataSource,
    } = this.state 
    return <div className='searchInput'>

      <AutoComplete
        className=""
        style={{ width: '100%' }}
        dataSource={dataSource.map(this.renderOption)}
        // dataSource={[]}
        onSelect={this.onSelect}
        onSearch={this.onSearch}
        placeholder="请输入要搜索的关键词"
        optionLabelProp="text"
      >
        <Input
          suffix={
            <Button
              className="search-btn"
              style={{ marginRight: -12 }}
              type="primary"
              onClick={this.search}
            >
              搜索
            </Button>
          }
        />
      </AutoComplete>

    </div>
  }
}

export default SearchInput// 

