import * as TYPES from "constants"
import { reducers,  } from 'utils'// 


export default (
    state = {}, 
    {
        type,
        payload,
    }
) => {
    switch (type) {
        // console.log(' type ： ', type,  )// 
        case TYPES.USER_INFO:
            return payload 
        case TYPES.LOUOUT:
            return {}
        case TYPES.LOAD:
            state.loading = payload
            return {
                ...state,
                loading: payload,
            }
        default:
            return state
    }
}


